<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 80px)" ref="getQuery"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.reproduction.reproductionBuild = true">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <div class="checking_box" :class="{addActive:addActive}">
      <checkInfoMap ref="checkInfoMap" @signOut="signOut" :serviceUrl="serviceUrl"></checkInfoMap>
    </div>
    <!-- 新建-->
    <reproductionAdd ref="reproduction" @getTableData="getClearQueryData"></reproductionAdd>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import checkInfoMap from './checkInfo_map'
import reproductionAdd from './reproductionAdd'
import {
  dituList,
  delDitu
} from '@/api/acquisitionTable'
export default {
  name: 'DataTable',
  data () {
    return {
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '图层名称',
          align: 'center',
          minWidth: 150,
          key: 'name'
        },
        {
          title: '发布时间',
          align: 'center',
          width: 250,
          key: 'createDate'
        },
        {
          title: '访问地址',
          align: 'center',
          minWidth: 150,
          key: 'url'
        },
        {
          title: '服务类型',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.type ? '矢量' : '影像')
          }
        },
        {
          title: '操作',
          key: 'address',
          width: 170,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.addActive = true
                    this.serviceUrl = params.row.url
                    this.$refs.checkInfoMap.initCom(params.row.type)
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.infoId = params.row.id
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该图层吗？',
                      onOk: () => {
                        this.infoDel()
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 查询框查询条件 */
      obj: {},
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      // 删除数据单条记录id
      infoId: '',
      /* 加载 */
      loading: false,
      /* 查看地图信息框状态 */
      addActive: false,
      /* 打开的地图服务地址 */
      serviceUrl: ''
    }
  },
  components: {
    QueryBox,
    // 底图管理查看
    checkInfoMap,
    // 添加底图
    reproductionAdd
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '图层名称'
        }
      ]
    }
  },
  created () {
    this.getSurveyTableList()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.pageSize = Math.floor((window.innerHeight - 350) / 48) + 1
      this.getSurveyTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getSurveyTableList()
    },
    /* 新添加底图之后返回的任务列表同时清空查询条件 */
    getClearQueryData () {
      this.queryData = {}
      this.pageNum = 1
      this.getSurveyTableList()
      this.$refs.getQuery.cleardata()
    },
    /* 获取字段列表 */
    getSurveyTableList () {
      this.loading = true
      dituList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getSurveyTableList()
    },
    /* axios: 删除数据 */
    infoDel () {
      delDitu({
        data: {
          id: [this.infoId]
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getSurveyTableList()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 信息查看地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
      .btn-add {
        background-color: #01c296;
        border-color: #01c296;
        &:hover, &:focus {
          background-color: #05ab85;
          border-color: #05ab85;
        }
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .checking_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
